body {
  background-color: #f5f5f5;
}

header {
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.hidden {
  display: none;
}

.search-form {
  background-color: gray;
  box-shadow: 0 0 10px 8px gray;
}

.search-form input {
  width: 300px;
}

.search-form input:active {
  border: 2px solid gray;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-evenly;
  display: flex;
}

.photo-card {
  box-sizing: border-box;
  max-width: 300px;
  max-height: 400px;
  background-color: azure;
  border-radius: 15%;
  margin: 10px 5px;
  overflow: hidden;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, .035), 0 6.7px 5.3px rgba(0, 0, 0, .047), 0 12.5px 10px rgba(0, 0, 0, .06), 0 22.3px 17.9px rgba(0, 0, 0, .07), 0 41.8px 33.4px rgba(0, 0, 0, .086), 0 0 80px rgba(0, 0, 0, .12);
}

.photo-card img {
  width: 100%;
  height: 75%;
  object-fit: cover;
}

.info {
  justify-content: space-evenly;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  margin: 20px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

